var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"app"}},[_c('Header'),_c('div',{staticClass:"wrapper"},[_c('p',[_vm._v(" – Туристический маршрут «По следам И.М. Гамова» проведет вас по местам и улицам города Благовещенск, которые принимали непосредственное участие в мартовских событиях 1918 года, закрепившимся в истории под названием «Гамовский мятеж». Многие из этих объектов представляют большую историческую и архитектурную ценность для нашего города и являются неотъемлемой частью его культуры. К вашему вниманию представлен маршрут, состоящий из шести точек. Каждое из мест расскажет вам одну небольшую историю. Эти истории тесно связанны друг с другом, поэтому, чем дальше вы будете продвигаться, тем больше интересных подробностей сможете узнать. Мы надеемся, что знания, которые вы получите во время пути, будут вам интересны. Желаем удачи! ")]),_c('h1',{},[_vm._v("Дом губернатора")]),_c('bg-map',{attrs:{"x":0.57,"y":0.66,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Дом построен в 1912 году. Здание кирпичное, двухэтажное, имеет два главных фасада. Композиция здания состоит из основного блока, разделенного тремя слабо выступающими ризалитами. Центральный ризалит сочетает в себе главный выход на первом этаже и открытую террасу на втором. Фланговые ризалиты завершены двухскатной крышей с аттиком. Центральный вход поддерживается двумя рядами колон дорического ордера. Строение пропорциональное и симметричное, что свойственно архитектуре классицизма. (Ленина, 144) ")]),_c('Slider',{attrs:{"left":"0.jpg","right":"0_new.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" В 9 часов вечера в губернаторском доме , захваченном РСД, было организованно собрание под руководством недавно прибывшего в Благовещенск А.М. Краснощекова, на котором обсуждалась сложившаяся ситуация. Приблизительно через полчаса после начала собрания дом был оцеплен отрядом гражданской милиции. В 11 часов вечера в губернаторский дом прибыла делегация Народного совета, потребовавшая снять караулы с государственных учреждений и разоружить подконтрольные совету воинские части. После предъявления ультиматума Ф.Н. Мухин, Ф.Н. Сюткин, А.М. Краснощеков в сопровождении ещё шести большевиков отправились в здание Амурского казачьего войска для осуществления переговоров. Оставшиеся в губернаторском доме члены совета решили сбежать, воспользовавшись темнотой, но были арестованы отрядом гражданской милиции и препровождены в тюрьму. В это же время гражданская милиция оцепила здание почты и разоружила находившихся внутри солдат. ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('h1',{},[_vm._v("Окружной суд")]),_c('bg-map',{attrs:{"x":0.4,"y":0.5,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Окружной суд был построен в конце XIX – начале XX века. Планировка здания соответствует его назначению и имеет два этажа. Кирпичная кладка, выполненная в разнообразном орнаменте, придает стенам утонченности. В центре основного фасада, выходящего на улицу Зейскую, располагается парадный вход, выделенный арочным проемом и фланкированный окнами с фронтонами. Центральная и боковые части здания акцентированы аттиком. Периметр крыши окружен балюстрадой, разделенной декоративными столбиками с вазонами. (Калинина, 27) ")]),_c('Slider',{attrs:{"left":"1.jpg","right":"1_new.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Здание Окружного суда являлось именно тем местом, где обсуждался вопрос о прекращении вооруженных действий в Благовещенске. Встреча между большевиками и амурским казачеством пошла 10 марта 1918 года в 11 часов утра. На встече присутствовали видные лица: И.М. Гамов, Г.Н. Кожевников, Р.А. Вертопрахов. В результате встечи было решено заключить перемирие, продлившееся всего двое суток. Также Позднее Окружной суд был задействован как центр решения городских вопросов и получил название \"Амурский губернский суд\". ")]),_c('Bio',{attrs:{"img":"gamov.png"}},[_vm._v(" – И.М. Гамов атаман Амурского казачьего войска. В марте 1918 года возглавил так называемый “Гамовский мятеж”, направленный на свержение большевистской власти. ")]),_c('h1',{},[_vm._v("Универсальный магазин \"Кунст и Альберс\"")]),_c('bg-map',{attrs:{"x":0.8,"y":0.5,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Универсальный магазин «Кунст и Альберс» в Благовещенске был построен 1894 году по проекту военного инженера Эдуарда Ивановича Шеффера. За образец были взяты Московские торговые ряды (ГУМ). Здание двухэтажное, кирпичное. Планировочная композиция здания соответствует его назначению. Основная плоскость стен выполнена из красного кирпича, что является средством выразительности. На этой основе хорошо виден фасад здания, оштукатуренный и окрашенный в белый цвет. Главный из фасадов выглядит парадно и представительно. Огибая угловую часть здания, он плавно перетекает в боковой не менее богатый на детали фасад. Здание решено в русском стиле, одной из разновидностей эклектики, которую называют «русское узорочье». Апогеем всей композиции является угловая килевидная кровля с металлическим декоративным гребнем. (Ленина, 165) ")]),_c('Slider',{attrs:{"left":"2.jpg","right":"2_new.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Торговый Дом «Кунст и Альберс» владел капиталом более 17 млн. рублей и был одним из основных поставщиков промышленного оборудования, систем электро и теплоснабжения, а также занимался комиссионно-транспортным обслуживанием. Во время мартовских событий 1918 года, Универсальный магазин \"Кунст и Альберс\" был одним из немногих торговых зданий, которые не подверглись тотальному разорению и мародерству. Вызванно такое везение было продуманностью владельцев, которые передали управление магазином китайским партнерам, они (как известно) имели полную неприкосновенность. Преступные элементы, пытавшиеся разграбить Торговый дом, понесли ответственность за совершенную ошибку. ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('h1',{},[_vm._v("Универсальный магазин \"И.Я. Чурин и Ко\"")]),_c('bg-map',{attrs:{"x":0.45,"y":0.38,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Универсальный магазин Торгового Дома «И.Я. Чурин и Ко» был построен в 1897 году. Здание двухэтажное, кирпичное. Протяженный основной фасад выходит на торговую площадь (сейчас сквер) и разделен на несколько массивных блоков с ярко выраженной средней частью. По краям боковых частей находятся купола, у основания которых размещены выразительные скульптуры. На фасаде также присутствуют элементы коринфского ордера, выделяющие отдельные части здания. Композиция включает в себя окна разного размера и конфигурации, что придает выразительности общей картине. Каждому крупному окну первого этажа соответствуют пара более узких окон второго. Фасад здания декорирован различными латунными элементами и вазами. Сооружение обладает эклектичной архитектурой, сочетая одновременно черты барокко и неоклассицизма. (Б. Хмельницкого, 1) ")]),_c('Slider',{attrs:{"left":"3.jpg","right":"3_new.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Благодаря удачному географическому положению на государственной границе с Китаем, в 1917 году в городе процветали торгово-купеческие отношения, развивалось большое количество прибыльного бизнеса и сельскохозяйственных предприятий. Торговые дома, коих в городе было множество, также могли похвастаться своим капиталом. Например, капитал торгового дома «И.Я. Чурин и Ко», который являлся крупнейшим торговым предприятием в городе, составлял в сумме более 30 млн. рублей. ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('h1',{},[_vm._v("Кондрашевская гостиница")]),_c('bg-map',{attrs:{"x":0.55,"y":0.55,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Здание перестроено в 1915 году из сгоревшего в 1912 г. магазина М.Т. Гурикова. Здание трехэтажное, кирпичное. Надстроенный сверху третий этаж не испортил композиционное решения здания, а напротив дополнил его. Фасад имеет своеобразную архитектуру. Частый ритм вытянутых оконных проемов вместе с ризалитами придает необычной живости главному фасаду. Здание симметрично, архитектурное направление не определено. (Ленина, 122) ")]),_c('Single',{attrs:{"img":"4.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Во время событий \"Гамовского мятежа\" здание гостиницы использовалось в качестве больницы. В нем на первом, втором и третьем этаже размещались раненые красногвардейцы, офицеры и казаки. Большие номера гостиницы были отданны постадавшим, а в маленьких размещался медперсонал. Кондрашевская гостиница не была единственным подобным местом, помимо неё в качестве медучреждений использовались школы и гимназии. ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('h1',{},[_vm._v("Железнодорожный вокзал")]),_c('bg-map',{attrs:{"x":0.67,"y":0.63,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Построен в 1908-1915 гг. Здание смежное (и одно- и двухэтажное), кирпичное. Вокзал выполнен в русском стиле и отражает все достоинства и черты этого архитектурного явления. Центральная часть здания – одноэтажная, выход выполнен в виде отдельного блока и специально смещен с оси симметрии. Торцы здания завершены двухэтажными объемами, различными по своей композиции. Венчающий центральный объем – четырехгранный шатер имеет декоративный металлический гребень. Цоколь выполнен из крупных каменных блоков. (Станционная, 70) ")]),_c('Single',{attrs:{"img":"6.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Железнодорожный вокзал города Благовещенска, во время событий \"Гамовского мятежа\" был эпицентром вооруженного конфликта. 7 и 8 марта он был исплользован защитниками города для отправки имправизированного бронепоезда, который должен был атаковать временную базу большевиков д. Астрахановку. Несмотря на все приготовления, операция потерпела крах, было убито большое количество человек, исчисляющееся стонями. Позднее, в ходе стихиного штурма (12 марта 1918 года), здание железнодорожного возала будет практически уничтожено точным артиллеристским попаданием, которое сильно повредит центраьлную часть здания. ")]),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('br'),_c('h1',{},[_vm._v("Благовещенское отделение Государственного Банка")]),_c('bg-map',{attrs:{"x":1.5,"y":0.6,"zoom":5,"h":1700,"offset":-20}}),_c('p',{staticClass:"bg cardo"},[_vm._v(" Начало строительства здания – 1898 г. Точная дата завешения строительства неизвестна (начало XX века). Двухэтажное, кирпичное здание скрыто от взора плотным насаждением деревьев. Раньше пространство перед ним было открыто и фасад здания отлично просматривался на протяжении набережной Амура. Постройка имеет симметричные фасады. Главный из них фланкирован двумя сильно выступающими ризалитами. Второй фасад, обращенный к городу, оснащен одинм осевым ризалитом. Все стены здания завершаются треугольными фронтами с высоким карнизом. Внутренний интерьер здания до сих пор украшает широкая двухмаршевая лестница с декоративными элементами в виде драконов. (Трудовая, 2) ")]),_c('Single',{attrs:{"img":"5.jpg"}}),_c('h1',{staticClass:"flex-start"},[_vm._v("Участие в событиях")]),_c('p',{staticClass:"bg"},[_vm._v(" Именно этот банк по заявлению большевиков был ограблен белогвардейцами, перед отступлением в Сахалян (сейчас г. Хэйхэ). По информации М. Губельмана офицрами и казаками было похищено более 36 миллионов рублей вместе с драгоценными металлами. Однако, по показаниям самих \"грабителей\", ценности, взятые из Благовещенского банка были не украденны, а спасены. ")])],1)],1)}
